import shopCarAnimation from '@/components/fly-in-cart/fly-in-cart.vue'
import common from '../../common/common.js'
import share from '@/components/share';
import {
	mapState
} from 'vuex';
export default {
	components: {
		share,
		shopCarAnimation
	},
	computed: {
		...mapState(['hasLogin', 'userInfo', 'bi', 'goods_id'])
	},
	data() {
		return {
			cart_count: 0,
			seller: {},
			goods: {},
			goods_id: 0,
			specClass: 'none',
			specSelected: [],

			favorite: true,
			shareList: [],
			imgList: [
				// {
				// 	src: 'https://gd3.alicdn.com/imgextra/i3/0/O1CN01IiyFQI1UGShoFKt1O_!!0-item_pic.jpg_400x400.jpg'
				// },
				// {
				// 	src: 'https://gd3.alicdn.com/imgextra/i3/TB1RPFPPFXXXXcNXpXXXXXXXXXX_!!0-item_pic.jpg_400x400.jpg'
				// },
				// {
				// 	src: 'https://gd2.alicdn.com/imgextra/i2/38832490/O1CN01IYq7gu1UGShvbEFnd_!!38832490.jpg_400x400.jpg'
				// }
			],
			desc: '',
			specList: [{
					id: 1,
					name: '尺寸',
				},
				{
					id: 2,
					name: '颜色',
				},
			],
			specChildList: [{
					id: 1,
					pid: 1,
					name: 'XS',
				},
				{
					id: 2,
					pid: 1,
					name: 'S',
				},
				{
					id: 3,
					pid: 1,
					name: 'M',
				},
				{
					id: 4,
					pid: 1,
					name: 'L',
				},
				{
					id: 5,
					pid: 1,
					name: 'XL',
				},
				{
					id: 6,
					pid: 1,
					name: 'XXL',
				},
				{
					id: 7,
					pid: 2,
					name: '白色',
				},
				{
					id: 8,
					pid: 2,
					name: '珊瑚粉',
				},
				{
					id: 9,
					pid: 2,
					name: '草木绿',
				},
			]
		};
	},
	async onLoad(options) {
		let that = this;
		console.log(that.hasLogin)
		if (!that.hasLogin) {
			let url = '/pages/public/login';
			uni.navigateTo({
				url
			})

		}

		//接收传值,id里面放的是标题，因为测试数据并没写id 
		let id = options.id;
		if (id) {
			// uni.setStorage({ //缓存用户登陆状态
			// 	key: 'goods_id',
			// 	data: id
			// });
			that.goods_id = id;
		}
		that.detail(that.userInfo, id);

	},
	methods: {
		/**
		 * 统一跳转接口,拦截未登录路由
		 * navigator标签现在默认没有转场动画，所以用view
		 */
		navTo(url) {
			 
			uni.navigateTo({
				url
			}) 
		},
		detail(userInfo, goods_id) {
			let that = this;
			uni.request({
				url: common.goods_editUrl, //仅为示例，并非真实接口地址。
				data: {
					is_mobile: 1,
					user_id: 0,
					func: 'show',
					user_id: userInfo.id,
					id: goods_id
				},
				method: 'POST',
				header: {
					'content-type': 'application/x-www-form-urlencoded'
				},
				success: function(res) {
					if (res.data.status == 1) {

						let carouselList = res.data.data.slider;

						// 						let cateList = res.data.category;
						// 
						// 						that.titleNViewBackground = carouselList[0].background;
						// 						that.swiperLength = carouselList.length;
						that.imgList = carouselList;
						// that.cateList = cateList;
						// 						let hotList = res.data.data;
						// 						console.log(hotList[0]['icon'])
						// 						that.hotList = hotList;
						that.goods = res.data.data;
						that.seller = res.data.seller;
						console.log(that.seller.id)

						that.goods_id = res.data.data.id;
						that.desc = res.data.data.pc_content;
						console.log(res.data.cart_count)
						that.cart_count = res.data.cart_count;

					} else {



					}



				}
			});

			//规格 默认选中第一条
			that.specList.forEach(item => {
				for (let cItem of that.specChildList) {
					if (cItem.pid === item.id) {
						that.$set(cItem, 'selected', true);
						that.specSelected.push(cItem);
						break; //forEach不能使用break
					}
				}
			})
			// that.shareList = that.$api.json('shareList');
		},

		//规格弹窗开关
		toggleSpec() {
			if (this.specClass === 'show') {
				this.specClass = 'hide';
				setTimeout(() => {
					this.specClass = 'none';
				}, 250);
			} else if (this.specClass === 'none') {
				this.specClass = 'show';
			}
		},
		//选择规格
		selectSpec(index, pid) {
			let list = this.specChildList;
			list.forEach(item => {
				if (item.pid === pid) {
					this.$set(item, 'selected', false);
				}
			})

			this.$set(list[index], 'selected', true);
			//存储已选择
			/**
			 * 修复选择规格存储错误
			 * 将这几行代码替换即可
			 * 选择的规格存放在specSelected中
			 */
			this.specSelected = [];
			list.forEach(item => {
				if (item.selected === true) {
					this.specSelected.push(item);
				}
			})

		},
		//分享
		share() {
			this.$refs.share.toggleMask();
		},
		//收藏
		toFavorite() {
			this.favorite = !this.favorite;
		},
		buy() {
			let that = this;
			console.log(that.userInfo.id);
			uni.request({
				url: common.cart_goods_addUrl, //仅为示例，并非真实接口地址。
				data: {
					"actiontype": 'buy',
					"is_mobile": 1,
					"user_id": that.userInfo.id,
					"article_id": that.goods.id,
					"goods_id": 0,
					"quantity": 1,
					"hot_id": 0
				},
				method: 'POST',
				header: {
					'content-type': 'application/x-www-form-urlencoded'
				},
				success: function(res) {
					console.log(res.data.status)
					if (res.data.status == 1) {

						uni.navigateTo({
							url: `/pages/order/createOrder`
						})

					} else {



					}



				}
			});

		},
		// 加入购物车
		addShopCar(e) {
			console.log('加入购物车');


			let that = this;
			console.log(that.userInfo.id);
			uni.request({
				url: common.cart_goods_addUrl, //仅为示例，并非真实接口地址。
				data: {
					"actiontype": 'add',
					"is_mobile": 1,
					"user_id": that.userInfo.id,
					"article_id": that.goods.id,
					"goods_id": 0,
					"quantity": 1,
					"hot_id": 0
				},
				method: 'POST',
				header: {
					'content-type': 'application/x-www-form-urlencoded'
				},
				success: function(res) {
					console.log(res.data.status)
					if (res.data.status == 1) {


						that.$refs.carAnmation.touchOnGoods(e);



						that.detail(that.userInfo, that.goods_id);

					} else {



					}



				}
			});

		},
		stopPrevent() {},

		//添加或修改成功之后回调
		refresh() {
			let that = this;

			this.detail(that.userInfo, that.goods_id);

		}
	},

}
